* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-size: 62.5%;
}

.App {
  height: 100vh;
  width: 100%;
  background: linear-gradient(to bottom left, #eee, #ccc);
}

.glass {
  display: inline-block;
  height: 7rem;
  margin-left: 1.5rem;
}

.title {
  margin: auto;
  display: flex;
  text-align: center;
  padding: 3rem 0 3rem;
  font-size: 3.8rem;
  align-items: center;
  justify-content: center;
}

.start-modal {
  height: 80rem;
  width: 120rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.start-modal-header {
  font-size: 3.2rem;
  margin-bottom: 2.4rem;
  color: rgb(39, 55, 80);
  font-weight: normal;
}

.instructions {
  text-align: center;
  font-size: 2.8rem;
  margin-bottom: 4rem;
  color: rgb(74, 81, 97);
  border: 2px solid rgb(54, 76, 109);
  border-radius: 2rem;
  padding: 4rem;
  background: #ededed;
  box-shadow: 1rem 1rem 4.5rem rgba(0, 0, 0, 0.15);
}

.instruction {
  margin: 2.6rem 0 2.6rem;
  max-width: 71rem;
  padding: 0 2rem 0 2rem;
}
.start-modal-btn {
  height: 8rem;
  width: 20rem;
  border-radius: 12px;
  font-size: 2.8rem;
  background-color: rgb(93, 131, 167);
  color: white;
  cursor: pointer;
  outline: none;
  border: none;
  box-shadow: 1rem 1rem 1.5rem rgba(0, 0, 0, 0.15);
}

.start-modal-btn:hover {
  background-color: rgb(54, 76, 109);
}

.start-modal-btn:active {
  transform: translateY(2%);
  background-color: rgb(114, 112, 112);
}

.game-panel {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.subject-title {
  text-align: center;
  font-size: 2.5rem;
  padding: 2rem 0 2rem;
}

.subject {
  border-radius: 0.8rem;
  padding: 1.1rem 1.5rem 1.5rem 1.5rem;
  background: rgba(255, 234, 180, 0.46);
  margin-left: 0.5rem;
  color: rgb(55, 55, 95);
}

.progress-section {
  position: absolute;
  right: 9rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 80%;
  width: 15rem;
}

.progress-title {
  font-size: 2.6rem;
  margin-bottom: 1.4rem;
}

.progress-bar {
  position: relative;
  height: 90%;
  width: 5rem;
  border: 1px solid grey;
  border-radius: 3rem;
  overflow: hidden;
  background: linear-gradient(
    to left,
    rgba(23, 116, 15, 0.7),
    rgba(23, 116, 15, 0.6)
  );
}

.line {
  width: 9.2rem;
  height: 4px;
  border-radius: 0.3rem;
  position: absolute;
  right: 5rem;
  background-color: rgb(149, 192, 152);
  z-index: 100;
}

.twenty-five {
  bottom: 26%;
}

.twenty-five::after {
  content: "25%";
  font-size: 2rem;
  text-align: center;
}

.fifty {
  bottom: 48%;
}

.fifty::after {
  content: "50%";
  font-size: 2rem;
  text-align: center;
}

.seventy-five {
  bottom: 70.5%;
}

.seventy-five::after {
  content: "75%";
  font-size: 2rem;
  text-align: center;
}

.progress {
  height: 100%;
  width: 100%;
  background-color: rgb(236, 243, 236);
  position: absolute;
  top: 0rem;
  transition: height 1s ease-in-out;
}

.underline {
  border-bottom: 0.2rem solid black;
  display: inline-block;
}

.card-display {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.card {
  width: 35rem;
  height: 16rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.1rem;
  background: linear-gradient(
    to bottom right,
    rgb(28, 35, 66),
    rgb(27, 54, 85)
  );
  color: white;
  margin: 1.8rem;
  border-radius: 1.2rem;
  cursor: pointer;
  transition: all 1s ease-in-out;
  transform-style: preserve-3d;
  box-shadow: 1.2rem 0rem 2rem rgb(0, 0, 0, 0.2);
}

.card:hover {
  background: linear-gradient(
    to bottom right,
    rgb(37, 46, 87),
    rgb(38, 76, 121)
  );
}

.answer {
  pointer-events: none;
  backface-visibility: hidden;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tick {
  transform: rotateX(180deg) translate(50%, 50%);
  height: 4rem;
  position: absolute;
  right: 50%;
  top: 50%;
  backface-visibility: hidden;
}

.spin {
  transform: rotateX(180deg);
  color: rgb(28, 44, 76);
  background: linear-gradient(
    to bottom right,
    rgb(20, 39, 24),
    rgb(23, 56, 34)
  );
}

.wobble {
  animation: wobble 0.5s linear;
}

@keyframes wobble {
  0% {
    transform: skewY(2deg);
  }
  15% {
    transform: skewY(-2deg);
  }
  30% {
    transform: skewY(2deg);
  }
  45% {
    transform: skewY(-2deg);
  }
  60% {
    transform: skewY(2deg);
  }
  75% {
    transform: skewY(-2deg);
  }
  90% {
    transform: skewY(2deg);
  }
  100% {
    transform: skewY(0deg);
  }
}

.fade {
  opacity: 0.4;
}

.continue-btn {
  position: absolute;
  bottom: 5rem;
  left: 50%;
  transform: translateX(-50%);
  height: 6.5rem;
  width: 18rem;
  font-size: 2.8rem;
  border-radius: 0.8rem;
  outline: none;
  cursor: pointer;
}

.continue-btn:hover {
  background: #ddd;
}

.complete-modal {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 30rem;
  width: 60rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  font-size: 5rem;
  background: linear-gradient(to top right, rgb(29, 49, 32), rgb(57, 105, 69));
  border-radius: 1.5rem;
  box-shadow: 1.5rem 1.5rem 0.8rem rgba(0, 0, 0, 0.2);
  color: white;
}

.game-complete-modal {
  width: 110rem;
  height: 40rem;
  padding-top: 10rem;
  padding-bottom: 10rem;
}

.complete-msg {
  display: flex;
  align-items: center;
}

.game-complete-msg-container {
  border: 3px solid rgb(235, 209, 122);
  padding: 3rem;
  border-radius: 2rem;
}

.game-complete-msg {
  font-size: 4rem;
  text-align: center;
  margin-right: 6rem;
}

.game-complete-msg p {
  border-color: rgb(235, 209, 122, 0.7);
}

.game-complete-msg-line {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.thumbs-up {
  display: inline-block;
  position: relative;
  height: 7rem;
  margin-left: 2rem;
  top: -1rem;
  opacity: 0;
  transition: opacity 0.6s ease-in;
}

.thumbs-up-complete {
  height: 15rem;
}

.fade-in {
  opacity: 1;
}
